*{
  font-family:'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;
}

header img {
  margin: 20px auto;
  display: block;
  width: 200px;
  border: 2px solid rgb(12, 11, 11);
  border-radius: 15px 50px;
}

aside {
  border-radius: 15px 50px;
  margin: 0 auto;
  display: block;
  height: 250px;
  width: 250px;
  background-size:auto;
  overflow: hidden;
  box-shadow: 0 0 100px 10px #fff;
  transition: all ease 0.3s;
}

aside:hover {

  border-radius: 35px;
  height: 350px;
  width: 350px;
  box-shadow: 0 0 70px 10px #fff;
}

aside:hover header img {
  animation: profile_image 2000ms linear both;
  border-radius: 35px;
}

header {
  text-align: center;
}

header h1 {
  position: relative;
  text-align: center;
  color: #fff;
  text-shadow: 1px 1px rgba(0, 0, 0, 0.5);
  font-size: 32px;
  line-height: 32px;
  display: inline-block;
  padding: 10px;
  transition: all ease 0.25s;
 
}

aside:hover header h1 {
  margin-top: 0px;
  outline: 0 solid #fff;
 }

header h2 {
  text-align: center;
  color: #fff;
  text-shadow: 1px 1px rgba(0, 0, 0, 0.5);
  font-size: 24px;
  font-weight: normal;
  line-height: 0px;
  margin: 0;
}

.profile-bio {
  margin-top: 5px;
  padding: 1px 10px 20px 1px !important;
  transition: all linear 1.5s;
  color: #fff;
  font-size: 24px;
  opacity: 0;
  background: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0) 0%,
    rgba(0, 0, 0, 0.42) 49%,
    rgba(0, 0, 0, 0.61) 100%
  );
}

aside:hover .profile-bio {
  opacity: 1;
}

.profile-bio p:first-child {
  text-align: center;
  font-size: 22px;
}

.profile-social-links {
  position: relative;
  margin-top: -440px;
  margin-left: -100px;
  list-style-type: none;
  opacity: 0;
  transition: all ease 0.5s;
}

aside:hover .profile-social-links {
  margin-left: -30px;
  opacity: 1;
}

.profile-social-links li img {
  width: 30px;
  background: #fff;
  border-radius: 50%;
  padding: 5px;
}

.about-container{
  padding: 2rem;
}



